export default function polizeistation(currentLat, currentLong) {
  //Alle Polizeistationen die nicht Wien oder Nieder√∂sterreich sind l√∂schen
  const polizeiWienNieder√∂sterreich = polizeistationen.filter(
    (elem) => elem.Land == 'Wien' || elem.Land == 'Nieder√∂sterreich',
  );

  let lat1 = currentLat;
  let lon1 = currentLong;

  //leeres Array definieren
  let arrayfertig = [];

  //Alle Polizeistationen durchgehen
  polizeiWienNieder√∂sterreich.forEach((elem) => {
    //Koordinaten von der Station holen
    let lat2 = elem.Y;
    let lon2 = elem.X;

    //Koordinaten umbauen auf . (16,3131065 -> 16.3131065)
    let lat2b = lat2.replace(',', '.');
    let lon2b = lon2.replace(',', '.');

    //String in Number konvertieren
    let lat2f = Number(lat2b);
    let lon2f = Number(lon2b);

    //Distanz zwischen Schule und Polizeistation berrechnen
    const distanz = distance(lat1, lat2f, lon1, lon2f);

    //Objekt mit Polizeistationdaten und Distanz erstellen
    let obj = { station: elem, distanz: distanz };
    //In vorhin definiertes Array pushen
    arrayfertig.push(obj);
  });

  //Standard setzen
  let smallest = arrayfertig[0].distanz;
  //Kleiner Distanz als Standard finden
  for (let index = 0; index < arrayfertig.length; index++) {
    //√úberpr√ºfen und falls gefunden zuweisen
    if (smallest > arrayfertig[index].distanz) smallest = arrayfertig[index].distanz;
  }
  console.log(smallest);

  //passende Polizeistation zu der Distanz finden
  const gefunden = arrayfertig.find((elem) => elem.distanz == smallest);

  return gefunden;
}

//Funktion distanz berechnen
function distance(lat1, lat2, lon1, lon2) {
  // The math module contains a function
  // named toRadians which converts from
  // degrees to radians.
  lon1 = (lon1 * Math.PI) / 180;
  lon2 = (lon2 * Math.PI) / 180;
  lat1 = (lat1 * Math.PI) / 180;
  lat2 = (lat2 * Math.PI) / 180;

  // Haversine formula
  let dlon = lon2 - lon1;
  let dlat = lat2 - lat1;
  let a =
    Math.pow(Math.sin(dlat / 2), 2) +
    Math.cos(lat1) * Math.cos(lat2) * Math.pow(Math.sin(dlon / 2), 2);

  let c = 2 * Math.asin(Math.sqrt(a));

  // Radius of earth in kilometers. Use 3956
  // for miles
  let r = 6371;

  // calculate the result
  return c * r;
}
